export default {
	props: ['obj'],
	data() {
		return {
			randomColor:'',
		}
	},
	methods: {
		navigator(list) {
			let url = ''
			if (list.type == this.$constData.contentType[0].key || list.type == this.$constData.contentType[2].key) {
				url = 'details'
			} else if (list.type == this.$constData.contentType[1].key) {
				url = 'detailsAtc'
			}
			console.log(`/pages/vip/course/${url}/${url}?id=${list.id}&id1=$(list._id)`)
			// uni.navigateTo({
			// 	url:`/pages/vip/course/${url}/${url}?id=${list.id}&id1=$(list._id)`
			// })
		},
		getTime(date) {
			this.getrandomColor()
			var t = parseInt(date)
			var a = new Date(t)
			var y = a.getFullYear()
			var m = 1 + a.getMonth()
			var d = a.getDate()
			var time = y + '年' + m + '月' + d + '日'
			return time
		},
		getrandomColor(){//随机赋值一种渐变色
			this.randomColor = this.$constData.colorData[Math.floor(Math.random()*this.$constData.colorData.length)]
		},
	}
}
